<template>
  <div id="classify">
    <div class="main-container">
      <div class="padding-md">
        <div class="main">
          <div class="design smart-widget widget-dark-blue">
            <div class="smart-widget-header"><span class="text m-left-sm"><i class="icon iconfont icon-createtask"></i></i> </span></div>
            <div class="tab_addCon">
              <router-link to="/addClassify"><button class="btn btn-success" id="add_but" ></button></router-link>
            </div>
            <table class="table table-hover text-center">
                <thead>
                  <th width="25%">ID</th>
                  <th width="25%"></th>
                  <th width="25%"></th>
                  <th width="80px"></th>
                </thead>
                <tbody>
                  <tr v-for="val in classifyTab">
                    <td>{{val.id}}</td>
                    <td>{{val.title}}</td>
                    <td>{{val.sort}}</td>
                    <td><div class="button-group">
                    <a class="button border-edit" href="javascript:void(0)"  @click="edit('#classifyEidt',val)"></a>
                    <a class="button border-del" href="javascript:void(0)" @click='del(val)' ></a>
                  </div></td>
                </tr>
                </tbody>
              </table>
              <div class="page">
                <!-- <span class="demonstration"></span> -->
                <el-pagination
                  @size-change="handleSizeChange"
                  @current-change="handleCurrentChange"
                  :current-page="currentPage"
                  :page-sizes="pagesizes"
                  :page-size="pagesize"
                  layout="total, sizes, prev, pager, next, jumper"
                  :total="len">
                </el-pagination>
              </div>
          </div>
          <classify-edit
              @childEditdata="pEditdata"
              @changeEditshow="pEditshow"
              :pData="editData"
              v-show="editshow"
          ></classify-edit>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import addClassify from './addClassify'
import classifyEdit from './classifyEdit'
import $ from 'jquery'
export default {
  name: "classify",
  components:{
    classifyEdit,
    // bootpage
  },
  mounted() {
    if(localStorage.getItem('classifyData')){
      this.classoldTab = JSON.parse(localStorage.getItem('classifyData'));
    }else{
      this.classoldTab = [
        {
          id:1,
          title:'',
          keytitle:'',
          keydesc:'',
          sort:10
        },
        {
          id:2,
          title:'',
          keytitle:'',
          keydesc:'',
          sort:11
        },
        {
          id:3,
          title:'',
          keytitle:'',
          keydesc:'',
          sort:6
        },
        {
          id:4,
          title:'',
          keytitle:'',
          keydesc:'',
          sort:10
        },
        {
          id:5,
          title:'',
          keytitle:'',
          keydesc:'',
          sort:2
        },
      ]
    }
    //
    this.len = this.classoldTab.length;
    //
    this.pagesizes=[5,20,50,100];
    //8
    this.pagesize = this.pagesizes[0];
    //
    this.start = (this.currentPage-1)*this.pagesize;
    //
    this.end = this.start+this.pagesize;
    //
    for(let i =this.start;i<this.end;i++){
      this.classoldTab?this.classifyTab.push(this.classoldTab[i]):[];
    }
    // console.log(this.start,this.end);
    // console.log(this.classifyTab.length);
    // console.log(this.classifyTab);
  },
  data(){
    return{
      //localStorage
      classoldTab:[],
      //
      classifyTab:[],
      editshow:false,
      editData:{},
      //
      currentPage:1,
      //
      len:0,
      //
      pagesizes:[],
      //
      pagesize:0,
      //
      start:0,
      //
      end:0
    }
  },
  methods: {
    del(val) {
      this.classifyTab=this.classifyTab.filter(e=>e.id !=val.id)
      this.classoldTab=this.classoldTab.filter(e=>e.id !=val.id)
      localStorage.setItem('classifyData',JSON.stringify(this.classoldTab))
    },
    edit(selector,val){
      this.editData = Object.assign(val);
      this.editshow = true;
      setTimeout(()=>{
        let anchor = $(selector)[0].offsetTop;
        document.body.scrollTop = anchor;
      },10)
    },
    pEditdata(data){
      this.classifyTab.forEach((e,i)=>{
        if(e.id == data.id){
          this.classifyTab.splice(i,1,data)
        }
      })
      localStorage.setItem('classifyData',JSON.stringify(this.classifyTab))
    },
    pEditshow(bool){
      this.editshow = bool;
    },
    handleSizeChange(val) {
      //  console.log(` ${val} `);
       this.pagesize = val;
       //
       this.start = (this.currentPage-1)*this.pagesize;
       //
       this.end = this.start+this.pagesize;
      //  console.log(this.pagesize,this.currentPage);
     },
     handleCurrentChange(val) {
       this.len = this.classoldTab.length;
       this.currentPage = val;
       this.start = (this.currentPage-1)*this.pagesize;
       //
       this.end = this.start+this.pagesize;
      //  console.log(`: ${val}`);
    }
  },
  watch:{
    currentPage:function(){
        this.classifyTab=[];
        if(this.end>this.len){
          this.end = this.len;
        }
        // console.log(this.start,this.end);
        //
        for(let i =this.start;i<this.end;i++){
          this.classoldTab?this.classifyTab.push(this.classoldTab[i]):[];
        }
    },
    pagesize:function(){
      this.classifyTab=[];
      if(this.end>this.len){
        this.end = this.len;
      }
      // console.log(this.start,this.end);
      //
      for(let i =this.start;i<this.end;i++){
        this.classoldTab?this.classifyTab.push(this.classoldTab[i]):[];
      }
    }
  }
}
</script>
<style scoped>
/**/
.block{
  margin:0 50%;
}
#add_but a{
  color:#fff;
}
.tiptitle{
  display:inline-block;
  vertical-align: middle;
  padding:8px;
}
#inputSuccess11,#inputSuccess1{
  width:50%;
}
.classify1{
  margin-bottom: 15px;
}
#classify-submit,#classify-cancel{
  width:100px;
  margin-left:100px;
}
#classify-cancel{
  margin-left:30px;
}
.page{
  width:100%;
  height:40px;
  overflow: hidden;
  position: relative;
}
.el-pagination {
  white-space: nowrap;
  padding: 2px 5px;
  color: #48576a;
  width: 50%;
  position: absolute;
  right: 0;
  text-align: center;
}
</style>



