<template>
  <div id="addcon">
    <div class="main-container">
      <div class="padding-md">
        <div class="main">
          <div class="design smart-widget widget-dark-blue">
            <div class="smart-widget-header"><span class="text m-left-sm"><i class="icon iconfont icon-document"></i></span></div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess1"></label>
              <input type="text" class="form-control" id="inputSuccess1" aria-describedby="helpBlock2" v-model="initaddData.title">
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess2"></label>
              <el-upload
                class="upload-demo"
                action="https://jsonplaceholder.typicode.com/posts/"
                :on-change="handleChange"
                :file-list="fileList">
                <el-button size="small" type="primary" id="picload"></el-button>
                <div slot="tip" class="el-upload__tip">jpg/png500kb</div>
              </el-upload>
            </div>
            <!-- Split button -->
            <div class="btn-group">
              <label class="control-label"></label>
              <select name="cid" class="input" style="width:120px; " v-model="opt">
                <option v-for="option in selected">{{option.txt}}</option>
              </select>
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label"></label>
              <div class="field">
                 <input id="ishome"  class="fileedit" type="checkbox" :checked="initaddData.attr == ''" @change="checkChange1"/>
                 <input id="isvouch" class="fileedit" type="checkbox" :checked="initaddData.attr == ''" @change="checkChange2"/>
                 <input id="istop" class="fileedit" type="checkbox" :checked="initaddData.attr == ''" @change="checkChange3"/>
              </div>
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess3"></label>
              <textarea class="form-control" rows="5" id="inputSuccess3" v-model="initaddData.desc"></textarea>
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess4"></label>
              <input type="text" class="form-control" id="inputSuccess4" aria-describedby="helpBlock2" v-model="initaddData.keyTitle">
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess5"></label>
              <input type="text" class="form-control" id="inputSuccess5" aria-describedby="helpBlock2" v-model="initaddData.sort">
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" ></label>
              <!-- <input type="text" class="form-control" aria-describedby="helpBlock2" v-model="initaddData.time"> -->
              <span>{{initaddData.time}}</span>
            </div>
            <div class="form-group has-success clearfix">
              <label class="control-label" for="inputSuccess6"></label>
              <input type="text" class="form-control" id="inputSuccess6" aria-describedby="helpBlock2" v-model="initaddData.count">
            </div>
            <button class="btn btn-success btn-sm no-shadow" id="add-submit" @click="submit"></button>
            <button class="btn btn-success btn-sm no-shadow" id="add-cancel" @click="cancel"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import $ from 'jquery';
export default {
  name: "addcon",
  mounted() {
    localStorage.setItem('addData',JSON.stringify(this.addData))
    if(localStorage.getItem('classifyData')){
      let arr = JSON.parse(localStorage.getItem('classifyData'));
      arr.forEach((e,i)=>{
        this.selected.push({
          txt:e.title,
          val:e.id
        })
      })
      this.opt = this.selected[0].txt;
    }else{
      this.addData.forEach(e=>{
        this.selected.push({
          txt:e.classTitle,
          val:e.id
        })
      })
      this.opt = this.selected[0].txt;
    }
    this.addData = JSON.parse(localStorage.getItem('addData'));
  },
  data() {
    return {
      fileList: [],
      initaddData:{
        title:'',
        img:'',
        attr:'',
        classTitle:'',
        desc:'',
        keyTitle:'',
        sort:'',
        time:'',
        count:'',
        checked:false
      },
      addData:[
        {
          id:1,
          title:'POLO',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170803_8aba64didf76gb2b2186hg37be4f5_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:' ',
          keyTitle:'',
          sort:11,
          time:'2017-7-3',
          count:15,
          checked:false
        },
        {
          id:2,
          title:'2017',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170807_1f1i3hajaajieig66546i268k0e7c_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'9910~~',
          keyTitle:'/',
          sort:44,
          time:'2017-5-6',
          count:222,
          checked:false
        },
        {
          id:3,
          title:'',
          img:'http://s3.mogucdn.com/p2/161203/84736276_5956b0aggced17j01792b197i45bf_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'2',
          keyTitle:',,,',
          sort:10,
          time:'2017-2-6',
          count:34444,
          checked:false
        },
        {
          id:4,
          title:'600g',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170830_4hdcack5845a4dcek98ckb3k24lc9_800x800.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'1773',
          keyTitle:'',
          sort:13,
          time:'2017-9-2',
          count:2333444,
          checked:false
        },
        {
          id:5,
          title:'',
          img:'http://s3.mogucdn.com/p2/160822/155173105_01ed3603gaejkb4d55abje193b4ee_649x974.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'',
          keyTitle:'932',
          sort:10,
          time:'2015-2-6',
          count:44466,
          checked:false
        },
      ],
      selected:[],
      opt:''
    }
  },
  methods: {
    handleChange(file) {
      this.initaddData.img=file.url;
    },
    checkChange1(){
      this.initaddData.attr=''
    },
    checkChange2(){
      this.initaddData.attr=''
    },
    checkChange3(){
      this.initaddData.attr=''
    },
    submit(){
      let max = maxId(this.addData)
      let time = new Date();
      time = time.getFullYear()+'-'+(time.getMonth()+1)+'-'+time.getDate();
        this.addData.unshift({
          id:++max,
          title:this.initaddData.title,
          img:this.initaddData.img,
          attr:this.initaddData.attr,
          classTitle:this.opt,
          desc:this.initaddData.desc,
          keyTitle:this.initaddData.keyTitle,
          sort:this.initaddData.sort,
          time:time,
          count:this.initaddData.count,
          checked:this.initaddData.checked
        })
      localStorage.setItem('addData',JSON.stringify(this.addData))
      this.initaddData.title=this.initaddData.desc=this.initaddData.keyTitle=this.initaddData.sort=this.initaddData.time=this.initaddData.count='';
      this.initaddData.attr=''
    },
    cancel(){
      this.initaddData.title=this.initaddData.desc=this.initaddData.keyTitle=this.initaddData.sort=this.initaddData.time=this.initaddData.count='';
      this.initaddData.attr=''
    }
  }
}


function maxId(data){
    let max = 0;
    data.forEach(e=>{
      if(max<e.id){
        max = e.id
      }
    })
    return max;
}

function getItem(data){
  return JSON.parse(localStorage.getItem('data')) || [];
}
</script>
<style  scoped>
#add-submit,#add-cancel{
  width:100px;
  margin-left:100px;
}
#add-cancel{
  margin-left:30px;
}
.el-button--primary{
  background: #2baab1;
  border: 1px solid #26969c;
}
.el-button--primary:hover{
  background:#32c5cd;
}
.upload-demo{
  margin-left:100px;
}
.el-upload{
  float: left;
}
.el-upload__tip{
  width:30%;
  float:left;
}
.btn-group .control-label{
  color:#2baab1;
}
.btn-default{
  border:1px solid #2baab1;
  background:#2baab1;
  color:#fff;
  margin-bottom: 15px;
}
.field{
  padding:5px;
  font-size:14px;
}
.fileedit{
  width: 15px;
  height: 15px;
  vertical-align: -3px;
  margin-right: 5px;
}
</style>



