<template>
  <div id="conpage">
    <div class="main-container">
      <div class="padding-md">
        <div class="main">
          <div class="design smart-widget widget-dark-blue">
            <div class="smart-widget-header">
              <span class="text m-left-sm">
                <i class="icon iconfont icon-ziliaoshouce-xianxing"></i> </span>
            </div>
            <div class="padding border-bottom">
              <ul class="search" style="padding-left:10px;">
                <li>
                  <button class="btn btn-success" id="checkAll">
                    <router-link to="/addcon"></router-link>
                  </button>
                  <button class="btn btn-success" id="allData" @click="dataAll" > </button>
              </li>
                <li></li>
                <li>
                  
                  <select name="s_ishome" class="input" @change="attr1($event)">
                    <option value=""></option>
                    <option value="1"></option>
                    <option value="0"></option>
                  </select>
                  &nbsp;&nbsp;
                  
                  <select name="s_isvouch" class="input" @change="attr2($event)">
                    <option value=""></option>
                    <option value="1"></option>
                    <option value="0"></option>
                  </select>
                  &nbsp;&nbsp;
                  
                  <select name="s_istop" class="input" @change="attr3($event)">
                    <option value=""></option>
                    <option value="1"></option>
                    <option value="0"></option>
                  </select>
                </li>
                  <li>
                    
                    <select name="cid" class="input" style="width:120px; " v-model="opt" @change="selectedTxt">
                      <option v-for="option in selected" >{{option.txt}}</option>
                    </select>
                  </li>
                <li id="spe">
                  <input
                    type="text"
                    placeholder=""
                    class="input"
                    style="width:200px; line-height:17px;display:inline-block"
                    v-model="searchVal"
                  />
                  <a href="javascript:;" class="button searchbtn" @click="search" > </a>
                </li>
              </ul>
            </div>
            <!--  table-->
            <table class="table table-hover text-center">
              <thead>
                <tr>
                  <th width="80">ID</th>
                  <th></th>
                  <th></th>
                  <th width="25%"></th>
                  <th></th>
                  <th></th>
                   <th width="120"></th>
                  <th></th>
                  </tr>
              </thead>
              <tbody>
                <tr v-for="(val,key) in tablist">
                  <td>
                    <input type="checkbox"  v-model="val.checked"/>
                   {{val.id}}
                 </td>
                  <td><input type="text" :value="val.sort" style="width:50px; text-align:center; border:1px solid #ddd; padding:7px 0;" /></td>
                  <td width="10%"><img :src="val.img" alt="" width="70" height="50" /></td>
                  <td>{{val.title}}</td>
                  <td><font color="#00CC99">{{val.attr}}</font></td>
                  <td>{{val.classTitle}}</td>
                  <td>{{val.time}}</td>
                  <td>
                    <div class="button-group">
                      <a class="button border-edit" href="javascript:void(0)" @click="edit(val,'#conedit')"> </a>
                      <a class="button border-del" href="javascript:;" @click="del(val)"> </a>
                    </div>
                  </td>
                </tr>
                <tr>
                  <td style="text-align:left; padding:19px 0;padding-left:20px;">
                    <input
                      type="checkbox"
                      id="checkall"
                      v-model="all"
                      @click="checkedAll"
                      :checked="computAll"
                    />
                  </td>
                  <td colspan="7" style="text-align:left;padding-left:20px;">
                    <a href="javascript:;" class="button delall border-del" style="padding:5px 15px;" @click="delAll"></a>
                    <a href="javascript:;" style="padding:5px 15px; margin:0 10px;" class="button sort" @click="sortData"> </a>
                  </td>
                </tr>
              </tbody>
            </table>
            <div class="page">
              <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="currentPage"
                :page-sizes="pagesizes"
                :page-size="pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="len">
              </el-pagination>
            </div>
          </div>
          <!--  -->
          <conedit
            v-show="editshow"
            @changeShow="pchangeShow"
            :pData="editData"
            :pSel="selected"
            :pShow="editshow"
            @childEditdata="pEditdata"
          ></conedit>
      </div>
    </div>
    </div>
  </div>
</template>
<script>
import conedit from './conedit'
import $ from 'jquery'
export default {
  name: "conpage",
  components: {
    conedit
  },
  mounted() {
    if(localStorage.getItem('addData')){
      this.initTablist = JSON.parse(localStorage.getItem('addData'));
    }else{
      this.initTablist = [
        {
          id:1,
          title:'POLO',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170803_8aba64didf76gb2b2186hg37be4f5_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:' ',
          keyTitle:'',
          sort:11,
          time:'2017-7-3',
          count:15,
          checked:false
        },
        {
          id:2,
          title:'2017',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170807_1f1i3hajaajieig66546i268k0e7c_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'9910~~',
          keyTitle:'/',
          sort:44,
          time:'2017-5-6',
          count:222,
          checked:false
        },
        {
          id:3,
          title:'',
          img:'http://s3.mogucdn.com/p2/161203/84736276_5956b0aggced17j01792b197i45bf_640x960.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'2',
          keyTitle:',,,',
          sort:10,
          time:'2017-2-6',
          count:34444,
          checked:false
        },
        {
          id:4,
          title:'600g',
          img:'http://s3.mogucdn.com/mlcdn/c45406/170830_4hdcack5845a4dcek98ckb3k24lc9_800x800.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'1773',
          keyTitle:'',
          sort:13,
          time:'2017-9-2',
          count:2333444,
          checked:false
        },
        {
          id:5,
          title:'',
          img:'http://s3.mogucdn.com/p2/160822/155173105_01ed3603gaejkb4d55abje193b4ee_649x974.jpg_468x468.jpg',
          attr:'',
          classTitle:'',
          desc:'',
          keyTitle:'932',
          sort:10,
          time:'2015-2-6',
          count:44466,
          checked:false
        },
      ]
    }
    if(localStorage.getItem('classifyData')){
      let arr = JSON.parse(localStorage.getItem('classifyData'));
      arr.forEach((e,i)=>{
        this.selected.push({
          txt:e.title,
          val:e.id
        })
      })
      // this.opt = this.selected[0].txt;
    }else{
      this.initTablist.forEach(e=>{
        this.selected.push({
          txt:e.classTitle,
          val:e.id
        })
      })
    }
    //
    this.len = this.initTablist.length;
    //
    this.pagesizes=[5,20,50,100];
    //8
    this.pagesize = this.pagesizes[0];
    //
    this.start = (this.currentPage-1)*this.pagesize;
    //
    this.end = this.start+this.pagesize;
    //
    for(let i =this.start;i<this.end;i++){
      this.initTablist?this.tablist.push(this.initTablist[i]):[];
    }
    this.oldTablist = Object.assign(this.tablist);
  },
  data(){
    return{
      initTablist:[],
      tablist:[],
      all:false,
      sortState:false,
      selected:[],
      oldTablist:[],
      opt:'',
      searchVal:'',
      editshow:false,
      editData:{},
      //
      currentPage:1,
      //
      len:0,
      //
      pagesizes:[],
      //
      pagesize:0,
      //
      start:0,
      //
      end:0
    }
  },
  methods:{
    //
    checkedAll(){
      this.tablist.forEach(e=>e.checked = this.all)
    },
    //
    del(val){
      this.tablist=this.tablist.filter(e=>e.id !=val.id)
      this.initTablist=this.initTablist.filter(e=>e.id !=val.id)
      localStorage.setItem('addData',JSON.stringify(this.initTablist))
    },
    //
    delAll(){
      this.tablist = this.tablist.filter(e=> !e.checked)
      this.initTablist = this.initTablist.filter(e=> !e.checked)
      localStorage.setItem('addData',JSON.stringify(this.initTablist))
    },
    //
    sortData(){
      this.sortState = !this.sortState;
      /*
        true:
        false:
      */
      if(this.sortState){
        //
        this.tablist.sort((a,b)=>{
          return b.sort-a.sort
        })
      }else{
        //
        this.tablist.sort((a,b)=>{
          return a.sort-b.sort
        })
      }
    },
    //
    attr1(ev){
      if(ev.target.value==1){
        this.tablist=this.tablist.filter(e=>e.attr === '');
      }else{
        this.tablist = this.oldTablist;
      }
    },
    //
    attr2(ev){
      if(ev.target.value==1){
        this.tablist=this.tablist.filter(e=>e.attr === '');
      }else{
        this.tablist = this.oldTablist;
      }
    },
    //
    attr3(ev){
      if(ev.target.value==1){
        this.tablist=this.tablist.filter(e=>e.attr === '');
      }else{
        this.tablist = this.oldTablist;
      }
    },
    selectedTxt:function(){
      this.tablist = this.oldTablist
      if(this.opt){
        this.tablist = this.tablist.filter(e=>e.classTitle == this.opt)
      }
    },
    search(){
      // console.log(this.searchVal);
      this.tablist=this.tablist.filter(e=>e.title.includes(this.searchVal))
      console.log(this.tablist);
    },
    dataAll(){
      if(localStorage.getItem('addData')){
        this.tablist = JSON.parse(localStorage.getItem('addData'));
      }else{
        this.tablist = this.initTablist;
      }
    },
    edit(val,selector){
      this.editshow = true
      this.editData = Object.assign(val);
      setTimeout(()=>{
        let anchor = $(selector)[0].offsetTop;
        document.body.scrollTop = anchor;
      },10)
    },
    pchangeShow(bool){
      this.editshow =bool;
    },
    pEditdata(data){
      // console.log(this.tablist);
      this.tablist.forEach((e,i)=>{
        if(e.id==data.id){
          this.tablist.splice(i,1,data)
        }
      })
      this.oldTablist.forEach((e,i)=>{
        if(e.id==this.tablist.id){
          this.oldTablist.splice(i,1,data)
        }
      })
      this.initTablist.forEach((e,i)=>{
        if(e.id==this.tablist.id){
          this.initTablist.splice(i,1,oldTablist)
        }
      })
      localStorage.setItem('addData',JSON.stringify(this.tablist))
    },
    handleSizeChange(val) {
      //  console.log(` ${val} `);
        this.len = this.initTablist.length;
       this.pagesize = val;
       //
       this.start = (this.currentPage-1)*this.pagesize;
       //
       this.end = this.start+this.pagesize;
     },
     handleCurrentChange(val) {
       this.len = this.initTablist.length;
       this.currentPage = val;
       this.start = (this.currentPage-1)*this.pagesize;
       //
       this.end = this.start+this.pagesize;
      //  console.log(`: ${val}`);
    }
  },
  watch:{
    currentPage:function(){
        this.tablist=[];
        if(this.end>this.len){
          this.end = this.len;
        }
        console.log(this.start,this.end);
        //
        for(let i =this.start;i<this.end;i++){
          this.initTablist?this.tablist.push(this.initTablist[i]):[];
        }

    },
    pagesize:function(){
      this.tablist=[];
      if(this.end>this.len){
        this.end = this.len;
      }
      // console.log(this.start,this.end);
      //
      for(let i =this.start;i<this.end;i++){
        this.initTablist?this.tablist.push(this.initTablist[i]):[];
      }
    }
  },
  computed:{
    //
    computAll:function(){
      if(this.tablist.length){
        this.all = this.tablist.every(e=>e.checked);
      }else{
        this.all = false;
      }
    }
  },
}
</script>
<style  scoped>
.page{
  width:100%;
  height:40px;
  overflow: hidden;
  position: relative;
}
.el-pagination {
  white-space: nowrap;
  padding: 2px 5px;
  color: #48576a;
  width: 50%;
  position: absolute;
  right: 0;
  text-align: center;
}
th{
  text-align: center
}
.searchbtn{
  padding:7px 12px;
}
#allData{
  width: 75px;
  height: 40px;
  margin-left: 0;
  font-size: 14px;
}
#spe{
  margin-right: 0
}
#checkAll a{
  color:#fff;
}
#con-submit,#con-cancel{
  width:100px;
  margin-left:100px;
}
#con-cancel{
  margin-left:30px;
}
.btn-default{
  border:1px solid #2baab1;
  background:#2baab1;
  color:#fff;
  margin-bottom: 15px;
}
.btn-group>.dropdown-toggle{
   border-radius: 4px;
 }
.btn-default:hover{
  background:#32c5cd;
}
.btn-group .control-label{
  color:#2baab1;
}
.field{
  padding:5px;
  font-size:14px;
}
.fileedit{
  width: 15px;
  height: 15px;
  vertical-align: -3px;
  margin-right: 5px;
}
.border-bottom{
  border-bottom: 0
}
</style>



